// Headings
// --------------------------------------------------
// stylelint-disable selector-max-type
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

@font-face {
  font-family: "FlandersArtSans-SerifThin";
  font-weight: $font-weight-normal;
  font-style: normal;
  font-display: block;
  src: url("../webfonts/serif/FlandersArtSerif-Thin.woff2")
      format("woff2"),
    url("../webfonts/serif/FlandersArtSerif-Thin.woff")
      format("woff");
}

h1 {
  @include h1;
  font-family: "FlandersArtSans-SerifThin";
}
h2 {
  @include h2;
}
h3 {
  @include h3;
}
h4 {
  @include h4;
}
h5 {
  @include h5;
}
h6 {
  @include h6;
}

// Body text
// --------------------------------------------------

p {
  margin-top: 0;
  // stylelint-disable-next-line primer/spacing
  margin-bottom: 10px;
}

small {
  // stylelint-disable-next-line primer/typography
  font-size: 90%;
}

blockquote {
  margin: 0;
}

// Lists
// --------------------------------------------------

ul,
ol {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

ol ol,
ul ol {
  list-style-type: lower-roman;
}

ul ul ol,
ul ol ol,
ol ul ol,
ol ol ol {
  list-style-type: lower-alpha;
}

dd {
  margin-left: 0;
}

// Code
// --------------------------------------------------

tt,
code {
  font-family: $mono-font;
  font-size: $font-size-small;
}

pre {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $mono-font;
  font-size: $font-size-small;
}

// Octicons
// --------------------------------------------------

// Move this over here as a temporary override to the octicons source repo
// instead of updating that upstream.
.octicon {
  vertical-align: text-bottom;
}
